<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title"> {{seller.name}}</h1>
         <div class="desc">
            <star :size="36" :score="seller.score"></star>
            <span class="text">({{seller.ratingCount}})</span>
            <span class="text">月售{{seller.sellCount}}单</span>
         </div>
        <ul class="remark">
            <li class="block">
                <h2>起送价</h2>
                <div class="content">
                  <span class="stress">{{seller.minPrice}}元</span>
                </div>
            </li>
            <li class="block">
                <h2>起送价</h2>
                <div class="content">
                  <span class="stress">{{seller.deliveryPrice}}元</span>
                </div>
            </li>
            <li class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="stress">{{seller.deliveryTime}}分钟</span>
              </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <span class="icon-favorite" :class="{'active':favorite}"></span>
          <span class="text">{{favoriteText}}</span>
        </div>
    </div>
      <split></split>
    <div class="bulletin">
      <h1 class="title">公告与活动</h1>
      <div class="content-wrapper">
          <p class="content">{{seller.bulletin}}</p>
       </div>
        <ul v-if="seller.supports" class="supports">
            <li class="supports-item" v-for="(item,index) in seller.supports"  v-bind:key="item.id">
                <span class="icon"  :class="classMap1[seller.supports[index].type]"></span>
                <span class="text" >{{ seller.supports[index].description }}</span>
            </li>
        </ul>
    </div>
       <split></split>
      <div class="pics">
        <h1 class="title"> 商家实景</h1>
        <div class="pic-wrapper" ref="picWrapper">
           <ul class="pic-list" ref="picList">
             <li class="pic-item" v-for="pic in seller.pics" v-bind:key="pic.id">
                <img :src="pic" width="120" height="90">
             </li>
           </ul>
       </div>
     </div>
       <split></split>
     <div class="info">
       <h1 class="title">商家信息</h1>
       <ul>
          <li class="info-item border-1px" v-for="info in seller.infos" v-bind:key="info.id">
            {{info}}
          </li>
       </ul>
     </div>
  </div> 
</div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll';
import {saveToLocal,loadFromLocal} from '../../common/js/store';
import star from "../star/star"
import split from '../split/split';
    export default{
      props:{
        seller:{
          type:Object
        }
      },
      data(){
        return{
         favorite:(() => {
           return loadFromLocal(this.seller.id,'favorite',false)
         })
        }
      },
      computed:{
        favoriteText(){
          return this.favorite?'已收藏':'未收藏'; //computed必须要有返回值
        }
      },
    created() {
      this.classMap1 = ['decrease','discount','special','guarantee','invoice'];
     },
      watch: {
      'seller'() {
        this.$nextTick(() => { //在下次 DOM 更新循环结束之后执行延迟回调。在修改数据之后立即使用这个方法，获取更新后的 DOM。
          this._initScroll();
          this._initPics();
        });
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll();
        this._initPics();
      });
    },
     methods:{
       toggleFavorite(event){
          if(event._constructed){
            return
          }
          this.favorite = !this.favorite;
          //这里想做的就是想把收藏的数量储存到localStorage
          saveToLocal(this.seller.id,'favorite',this.favorite);
       },
      _initScroll(){
        if(!this.scroll){
          this.scroll = new BScroll(this.$refs.seller,{
            click:true
          });
        }else{
          this.scroll.refresh();
        }
      },
      _initPics(){
        if(this.seller.pics){
         let picWidth = 120;
         let margin = 6;
         let width = (picWidth+margin)*this.seller.pics.length-margin;
         this.$refs.picList.style.width = width + 'px';
         //水平方向滚动
         this.$nextTick(() =>{
           if(!this.picScroll){
               this.picScroll = new BScroll(this.$refs.picWrapper,{
               scrollX:true,
               eventPassthrough: 'vertical'
           })
          }else{
             this.picScroll.refresh();
           }
         })
       }
      }
     },
     components:{
        star:star,
         split:split
      }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin';
  .seller
     position:absolute
     top:174px
     bottom:0
     left:0
     width:100%
     overflow:hidden
     .overview
        position:relative
        padding:18px
        .title
          margin-bottom:8px
          line-height:12px
          color:rgb(7,17,27)
          font-size:12px
        .desc
          padding-bottom:18px
          line-height:18px
          border-1px(rgba(7,17,27,0.1))
          .star
            display:inline-block
            margin-right:8px
            vertical-align:top
            .text
               margin-right:12px
               display:inline-block
               vertical-align:top
               font-size:10px
               color:rgb(77,85,93)
        .remark
          display:flex
          padding-top:18px
          .block
            flex:1
            text-align:center
            border-right:1px solid rgba(7,17,27,0.1)
            &:last-child
              border:none
            h2
              margin-bottom:8px
              line-height:4px
              font-size:10px
              color:rgb(147,153,159)
            .content
              font-size:0
              line-height:24px
              font-size:10px
              color:rgb(7,17,27)
              .stress
                font-size:24px 
        .favorite
          position:absolute
          right:18px
          top:18px
          text-align:center
          .icon-favorite
              display:block
              margin-bottom:4px
              line-height:24px
              font-size:24px
              color:#d4d6d9
              &.active
                color:red
          .text
              line-height:20px
              font-size:10px
              color:rgb(77,85,93) 
     .bulletin
        padding:18px 18px 0 18px
        .title
           margin-bottom:8px
           line-height:14px
           color:rgb(7,17,27)
           font-size:14px
        .content-wrapper
           padding:0 12px 16px 12px
           border-1px(rgba(7,17,27,0.1))
           .content
              line-height:24px
              font-size:12px
              color:rgb(240,20,20)
        .supports
          .supports-item
            padding:8px 12px
            font-size:0
            border-1px(rgba(7, 17, 27, 0.1))
            &:last-child
              border-none()
          .icon
              display:inline-block
              width:16px
              height:16px
              vertical-align:top
              margin-right:6px
              background-size:16px 16px 
              background-repeat:no-repeat
              &.decrease
                bg-image('decrease_4')
              &.discount
                bg-image('discount_4')
              &.special
                bg-image('special_4')
              &.guarantee
                  bg-image('guarantee_4')
              &.invoice
                  bg-image('invoice_4')
          .text
              line-height: 16px
              font-size: 12px
              color: rgb(7, 17, 27)
     .pics
        padding:18px 18px
        .title
          margin-bottom:12px
          line-height:14px
          color:rgb(7,17,27)
          font-size:14px
        .pic-wrapper
           width:100%
           overflow:hidden
           white-space:nowrap
          .pic-list
            font-size:0
            .pic-item
              display:inline-block
              margin-right:6px
              width:120px
              height:90px
              &:last-child
                margin:0
      .info
         padding:18px 18px 0 18px
         .title
            padding-bottom:12px
            font-size:14px
            border-1px(rgba(7,17,27,0.1))
            color:rgb(7,17,27)
            font-size:14px
        .info-item
          padding:12px
          line-height:16px
          border-1px(rgba(7,17,27,0.1))
          font-size:12px
          &:last-child
             border-none()
</style>


